html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: var(--light) !important;
  font-family: "Open Sans", sans-serif;
}

.padding {
  padding: 0.5rem 1rem;
}

.carousel-img {
  width: 100%;
}

.band-col {
  background-color: var(--primary);
}

.figure {
  margin-bottom: 0;
}

.figure-img {
  margin-bottom: 0 !important;
}

.nav-link {
  font-size: 1.6rem !important;
}

.card-img {
  height: 100%;
  max-height: 150px;
  width: auto;
}

.footer-hr {
  border-top: 1px solid;
  margin: 0 auto;
}

.footer-txt {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 500;
}
